.btn {
      border-radius: 5px;
      outline: none;
}

.btn:hover,
.btn:link,
.btn:visited {
      position: relative;
      cursor: pointer;
      padding: 0.3rem 0.5rem;
      transform: translateY(0px);
      transition: all 0.4s ease;

}

.btn:hover {
      background-color: var(--color-green);
      box-shadow: 0 2px 7px 0.5px rgba(var(--color-dark-1), 0.8);
      transform: translateY(-5px);
}

.icon {
      translate: translateY(0rem) scale(1);
      transition: all 0.2s;
      color: inherit;
      font-size: 1.2rem;
      position: relative;
}

.icon:hover,
i:active {
      color: var(--color-purple);
      translate: translateY(-1rem) scale(1.2);

}